/******************************************************************
HEADER STYLES
Styles specific to the site header
******************************************************************/

.is-mobile-menu-open {
    @extend .has-no-scroll-y;
}

.header {
    position: relative;
    min-height: 120px;
    background: $color-white;
    z-index: 11;
    transition: $transition-default;

    &__desktop {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }

    &__mobile {
        display: none;
        justify-content: space-between;
        align-items: center;
    }

    &__content {
        display: flex;
    }

    &__logo {
        margin: 0 50px;
    }

    &__menu {
        flex-grow: 1;
        flex-basis: 0;

        &:first-child {
            display: flex;
            justify-content: flex-end;
        }
    }

    @include below-desktop-xl {
        &__logo {
            margin: 0 40px;
        }
    }

    @include below-desktop-lg {
        &__logo {
            margin: 0 30px;
        }
    }

    @include below-desktop {
        position: fixed;
        top: 0;
        left: 0; right: 0;
        box-shadow: $shadow02;

        &__top {
            transition: $transition-default;
        }

        &__desktop {
            display: block;
            position: fixed;
            left: 0; right: 0; top: 0; bottom: 0;
            background: $color-white;
            padding-top: 180px;
            overflow-y: auto;

            visibility: hidden;
            opacity: 0;
            transform: translateY(0);
            transition: $transition-default;
            z-index: -1;

            .header-logo {
                display: none;
            }
        }

        &__mobile {
            display: flex;
        }

        &__main {
            display: flex;
            align-items: center;
            height: 80px;
        }

        &__logo {
            margin: 0 30px 0 0;
        }

        &__menu {
            flex-direction: column;
            justify-content: center;
        }
        
    }
}

.top-bar {
    background: $color04;
    height: 40px;
    width: 100%;

    &__inner {
        display: flex;
        justify-content: flex-end;
    }

    &__search {
        margin-left: 60px;
    }

    &__socials {
        display: flex;
        align-items: center;
        margin-left: 48px;
    }

    @include below-desktop {
        &__inner {
            justify-content: space-between;
            align-items: center;
            height: 40px;
        }

        &__search {
            display: none;
        }
    }
}

.header-logo {
    display: flex;
    align-items: center;
    z-index: 101;
  
    &__anchor {
        display: block;
        height: 72px;
        opacity: 1;
        transition: $transition-default;
  
        &:hover {
            opacity: $opacity-md;
            transition: $transition-default;
        }
    }
  
    &__img {
        display: block;
        height: 100%;
        max-width: initial;
    }

    @include below-desktop {
        &__anchor {
            height: 48px;
        }
    }
}

.header-nav {
    .menu {
        display: flex;

        &__item {
            position: relative;
            margin: 0 12px;
        }

        &__anchor {
            position: relative;
            display: flex;
            align-items: center;
            padding: 0 24px;
            height: 120px;
            max-width: 180px;
            text-align: center;
            text-decoration: none;
            font-weight: $font-weight-bold;
            line-height: $line-height-sm;
            transition: $transition-default;

            &:before {
                content: "";
                position: absolute;
                bottom: 0; left: 0; right: 0;
                height: 4px;
                width: 100%;
                background-color: $color02;
                opacity: 0;
                transition: $transition-default;
            }

            &:hover {
                &:before {
                    opacity: 1;
                    transition: $transition-default;
                }
            }

            &.is-active, &.is-ancestor {
                color: $color02;
            }
        }

        > .menu__item {
            &.menu__item--has-children {
                > .menu__anchor {
                    position: relative;
                    padding-right: 42px;
    
                    &:after {
                        content: "";
                        position: absolute;
                        top: calc(50% - 2px);
                        right: 20px;
                        background-image: url('../../assets/icons/icon-caron-menu.svg');
                        background-repeat: no-repeat;
                        background-size: contain;
                        height: 12px;
                        width: 12px;
                        opacity: 1;
                        transition: $transition-default;
                    }
    
                    @include above-desktop {
                        &:hover {
                            &:after {
                                opacity: $opacity-md;
                                transition: $transition-default;
                            }
    
                            ~ .menu__sub-menu {
                                transform: translateX(0);
                                opacity: 1;
                                visibility: visible;
                                transition: $transition-default;
                            }
                        }
                    }
                }

                .menu__sub-menu {
                    position: absolute;
                    top: 120px; left: 0;
                    padding: 24px 0 28px 0;
                    min-width: 320px;
                    background: $color-white;
        
                    transform: translateY(-8px);
                    opacity: 0;
                    visibility: hidden;
                    transition: $transition-default;
        
                    @include above-desktop {
                        &:hover {
                            transform: translateX(0);
                            opacity: 1;
                            visibility: visible;
                            transition: $transition-default;
                        }
                    }

                    &.is-active {
                        transform: translateX(0);
                        opacity: 1;
                        visibility: visible;
                        transition: $transition-default;
                    }

                    .menu__item {
                        margin: 0;
                    }
        
                    .menu__anchor {
                        display: block;
                        height: auto;
                        margin: 0;
                        padding: 16px 40px;
                        max-width: none;
                        text-align: left;

                        &:before {
                            display: none;
                        }
        
                        &.is-active {
                            color: $color02;
                            transition: $transition-default;
                        }
                        
                    }
                }
            }
        }

        @include below-desktop-xl {
            &__item {
                margin: 0 10px;
            }

            &__anchor {
                padding: 0 20px;
            }
        }

        @include below-desktop {
            width: 100%;
            flex-direction: column;
            justify-content: center;

            &__item {
                margin-bottom: 30px;
            }

            &__anchor {
                display: block;
                padding: 0 12px;
                max-width: 100%;
                height: initial;
                text-align: left;
                font-size: $font-size-md;

                &:before {
                    display: none;
                }
            }

            > .menu__item {
                &.menu__item--has-children {    
                    .menu__sub-menu {
                        position: relative;
                        top: 0; left: 0;
                        padding: 0;
                        height: 0;

                        &.is-active {
                            height: auto;
                        }

                        .menu__anchor {
                            padding-left: 40px; 
                            padding-bottom: 8px;
                            font-size: 1.6rem;
                        }
                    }
                }
            }
        }
    }
}

.language-switcher, .language-nav .menu {
    height: 100%;
    display: flex;
    align-items: center;
    margin: 0 -12px;
    font-size: $font-size-sm;

    &__item, .menu__item {
        padding: 0 12px;
    }
}

.mobile-menu-toggle {
    display: block;
    width: 48px;
    height: 48px;
    padding: 12px 14px;
	background-color: $color03;
	color: $color-white;
    font-size: 0;
	line-height: $line-height-sm;
    border-radius: $border-radius 0 $border-radius 0;
	transition: $transition-default;
  
    &:before, &:after {
        content: '';
        background-color: $color-white;
        display: block;
        height: 2px;
        width: 100%;
        transition: $transition-default;
    }
  
    &:before {
        box-shadow: 0 8px 0 $color-white;
        margin-bottom: 14px;
    }

    &:hover, &:focus {
		color: $color-white;
		background-color: $color02;
		transition: $transition-default;
	}
}

.is-scrolled {
    @include below-desktop {
        .header {
            margin-top: -40px;
            transition: $transition-default;
        }
    }
}

.is-mobile-menu-open {
    @extend .has-no-scroll-y;

    .mobile-menu-toggle {
        &:before {
            box-shadow: 0 0 0 rgba($color-white, 0);
            transform: translateY(8px) rotate(45deg);
        }
        &:after {
            width: 100%;
            transform: translateY(-8px) rotate(-45deg);
        }
    }

    .header {
        &__desktop {
            visibility: visible;
            opacity: 1;
            transform: translateY(0);
            transition: $transition-default;
        }
    }
}

.is-only-visible-mobile-menu {
    display: none;

    @include below-desktop {
        display: block;
    }
}