/******************************************************************
ITEM STYLES
Styles for granular items (mainly part of lists)
******************************************************************/

.blog-card {
    display: flex;
    //border-radius: $border-radius;
    border-radius: $border-radius 0 $border-radius 0;
    background: $color-white;
    transition: $transition-default;

    &__content {
        padding: 36px 40px;
        min-height: 240px;
        //border: 1px solid $color05;
        border-left: none;
        //border-top-right-radius: $border-radius;
        border-radius: 0 0 $border-radius 0;
        flex: 2;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    &__image {
        flex: 1;
        min-width: 240px;
        background-color: $color05;
        background-size: cover;
        background-position: 50% 50%;
        border-top-left-radius: $border-radius;
        border-radius: $border-radius 0 0 0;
    }

    &__date {
        color: $color02;
    }

    &__title {
        margin: 3px 0 0 0;
    }

    &__excerpt {
        display: none;
        margin: 16px 0 0 0;
    }

    &__options {
        margin-top: 16px;
    }
    
    &--size-lg {
        @include above-desktop {
            height: 100%;
            flex-direction: column;
            border-radius: $border-radius 0 $border-radius 0;

            .blog-card__image {
                min-height: 45%;
                width: 100%;
                //border-radius: $border-radius $border-radius 0 0;
                border-radius: $border-radius 0 0 0;
            }

            .blog-card__content {
                //border: 1px solid $color05;
                //border-radius: 0 0 $border-radius $border-radius;
                border-radius: 0 0 $border-radius 0;
            }

            .blog-card__title {
                font-size: $font-size-xl;
                margin: 12px 0 0 0;
            }

            .blog-card__excerpt {
                display: block;
                color: $color-text;
                margin-top: 20px;
            }
        }
    }

    &:hover {
        transform: translateY(-8px);
        transition: $transition-default;

        .blog-card__more {
            .icon {
                animation-name: buttonIconBounceRight;
                animation-duration: $trans-time-md;
                animation-direction: forwards;
            }
        }
    }

    @include below-mobile {
        &__image {
            min-width: 160px;
            max-width: 200px;
        }

        &__content {
            padding-left: 30px;
            padding-right: 30px;
        }
    }

    @include below-mobile-sm {
        &__image {
            display: none;
        }
    }
}

.month-card {
    display: inline-block;
    text-align: center;
    padding: 48px 20px;
    min-width: 170px;
    width: 100%;
    color: $color-white;
    border: 1px solid $color-white;
    border-radius: $border-radius;
    transition: $transition-default;

    &__month {
        line-height: 1;
        margin-bottom: 8px;
    }

    &__year {
        line-height: 1;
        font-size: $font-size-xl;
        font-weight: $font-weight-black;
    }

    &.is-inactive {
        opacity: $opacity-lg;
        transition: $transition-default;
    }

    &.is-active {
        opacity: 1;
        color: $color-white !important;
        border-color: $color02;
        background: $color02;
        transition: $transition-default;
    }

    &:hover {
        opacity: 1;
        color: $color01 !important;
        border-color: $color-white;
        background: $color-white;
        transform: translateY(-8px);
        transition: $transition-default;
    }
}

.event-card {
    display: block;
    padding: 60px 76px;
    background: $color-white;
    border-radius: $border-radius;
    transition: $transition-default;

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 -20px;
    }

    &__main, &__options {
        padding: 0 20px;
    }

    &__date {
        color: $color02;
    }

    &__title {
        font-size: 2.8rem;
        color: $color01;
        margin: 0;
        transition: $transition-default;
    }

    &:hover {
        transform: translateY(-8px);
        transition: $transition-default;

        .event-card__title {
            color: $color03;
            transition: $transition-default;
        }

        .button {
            color: $color-white;
            background-color: $color02;
            border-color: $color02;

            .icon {
                animation-name: buttonIconBounceRight;
                animation-duration: $trans-time-md;
                animation-direction: forwards;
            }
        }
    }

    &--archive {
        .event-card__title {
            color: $color03;
        }
    }

    @include below-mobile {
        padding: 40px 40px 48px 40px;

        &__title {
            font-size: $font-size-lg;
        }
        
        &__options {
            display: none;
        }
    }

    @include below-mobile-sm {
        padding: 40px 20px 48px 20px;
    }
}

.achievement {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 280px;

    &__text {
        margin-top: 16px;
        text-align: center;
    }

    &__number {
        display: block;
        color: $color03;
    }

    &__title {
        display: block;
        font-weight: $font-weight-bold;
        font-size: $font-size-lead;
    }

    @include below-mobile-sm {
        max-width: 100%;
    }
}

.search-card {
    position: relative;
    border-bottom: 1px solid $color05;
    padding-right: 76px;

    &__inner {
        padding: 36px 0 40px 0;
    }

    &__type {
        color: $color-gray;
        margin: 0 0 4px 0;
        font-size: $font-size-sm;
    }

    &__title {
        margin: 0;
    }

    &__excerpt {
        margin: 8px 0 0 0;
    }

    &__actions {
        position: absolute;
        top: calc(50% - 28px);
        right: 0;
    }

    @include below-mobile-sm {
        padding-right: 0;

        &__inner {
            padding: 20px 0 24px 0;
        }

        &__actions {
            display: none;
        }
    }
}