/******************************************************************
CONTENT
Main content blocks and sections that make up the site layout
******************************************************************/

.hero {
    position: relative;
    height: 600px;
    background: $color-white;
    overflow: hidden;
    z-index: 0;

    &:after {
        content: "";
        position: absolute;
        bottom: 0; left: 0; right: 0;
        display: block;
        height: 162px;
        width: 100%;
        background: transparent;
        background-image: url('../../assets/images/bg-wave-loop.svg');
        background-repeat: repeat-x;
        background-size: 2100px 162px;
        background-position: -80px top;
    }

    &__background {
        position: absolute;
        left: 0; top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
        font-size: 0;

        animation: slideRight 8s forwards infinite linear;
        opacity: 0;
        visibility: hidden;

        &.is-active {
            opacity: 1;
            visibility: visible;
        }
    }

    &__sponsor {
        display: block;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        height: 180px;
        width: auto;
        padding: 20px;
        background: $color-white;
        border-top-left-radius: $border-radius;
        border-bottom-left-radius: $border-radius;
        z-index: 2;
    }

    &__sponsor-mobile {
        display: none;
        position: relative;
        margin: 80px auto 0 auto;
        height: 72px;
        z-index: 3;
    }

    @include below-desktop-lg {
        &__sponsor {
            top: 40%;
        }
    }

    @include below-mobile {
        height: 420px;

        &__sponsor {
            display: none;
        }

        &__sponsor-mobile {
            display: block;
        }

        &__backgrounds {
            opacity: 0.5;
        }
    }
}

.quicklinks {
    position: relative;
    margin-top: -185px;
    z-index: 1;

    &__inner {
        padding: 0 0 40px 0;
    }

    &__items {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin: 0 -10px;
        padding-bottom: 40px;
        //border-bottom: 1px solid $color05;
    }

    &__item {
        flex: 1;
        min-width: 240px;
        max-width: 320px;
        padding: 0 10px;
    }

    @include below-desktop-lg {
        &__items {
            flex-wrap: wrap;
        }

        &__item {
            margin-bottom: 20px;
            max-width: 360px;
        }
    }

    @include below-mobile-sm {
        &__items {
            margin: 0;
        }

        &__item {
            padding: 0;
        }
    }
}

.blog-preview {
    position: relative;
    background: $color04;
    padding: 0 0 360px 0;

    &__inner {
        display: flex;
        margin: 0 -20px;
    }

    &__main, &__aside {
        flex: 1;
        padding: 0 20px;
    }

    &__alert {
        text-align: center;
        margin: 0 auto;
    }

    &--front {
        z-index: 1;
    }

    @include below-desktop-lg {
        &__main {
            flex: 2;
        }
        &__aside {
            flex: 3;
        }
    }

    @include below-desktop {
        &__inner {
            display: block;
        }

        &__main {
            margin-bottom: 20px;
        }
    }

    @include below-mobile {
        padding: 0 0 280px 0;
    }
}

.events-preview {
    position: relative;
    background: $color01;
    color: $color-white;
    padding: 160px 0 360px 0;

    &:before {
        content: "";
        position: absolute;
        top: -161px; left: 0; right: 0;
        display: block;
        height: 162px;
        width: 100%;
        background: transparent;
        background-image: url('../../assets/images/bg-wave-loop2.svg');
        background-repeat: repeat-x;
        background-size: 2100px 162px;
        background-position: -800px top;
        z-index: 1;
    }

    .events-preview__title {
        color: $color-white;
    }

    &__slider {
        margin-bottom: 96px;
    }

    &__alert {
        margin-bottom: 40px;
        text-align: center;
        opacity: 0.8;
    }

    &__options {
        margin-top: 80px;
    }

    @include below-mobile {
        padding: 0 0 300px 0;

        &__slider {
            margin-bottom: 60px;
        }
    }
}

.socials-activity {
    position: relative;
    background: $color02;
    padding: 160px 0 320px 0;
    color: $color-white;

    &:before {
        content: "";
        position: absolute;
        top: -161px; left: 0; right: 0;
        display: block;
        height: 162px;
        width: 100%;
        background: transparent;
        background-image: url('../../assets/images/bg-wave-loop3.svg');
        background-repeat: repeat-x;
        background-size: 2100px 162px;
        background-position: -280px top;
        z-index: 1;
    }

    .socials-activity__title {
        color: $color-white;
    }

    &__options {
        padding-top: 40px;
    }

    @include below-mobile {
        padding: 0 0 200px 0;
    }
}

.about {
    position: relative;
    background: $color04;
    padding: 100px 0 140px 0;

    &:before {
        content: "";
        position: absolute;
        top: -161px; left: 0; right: 0;
        display: block;
        height: 162px;
        width: 100%;
        background: transparent;
        background-image: url('../../assets/images/bg-wave-loop.svg');
        background-repeat: repeat-x;
        background-size: 2100px 162px;
        background-position: 200px top;
        z-index: 1;
    }

    &__items {
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
        margin: 100px -30px 0 -30px;
    }

    &__item {
        padding: 0 30px;
        margin-bottom: 60px;
    }

    &__content {
        display: flex;
        margin: 0 -30px;
    }

    &__content-left, &__content-right {
        flex: 1;
        padding: 0 30px;
        line-height: $line-height-lg;
    }

    &__gallery {
        margin-top: 60px;
    }

    @include below-mobile-sm {
        &__items {
            flex-direction: column;
        }

        &__content {
            flex-direction: column;
            align-items: center;
            text-align: center;
        }
    }
}

.blog, .events {
    padding-top: 100px !important;

    .blog__title, .events__title {
        margin-bottom: 20px;
    }

    &__categories {
        margin-bottom: 64px;
    }

    &__categories-mobile {
        display: none;
    }

    &__actions {
        margin-top: 80px;
        display: flex;
        justify-content: center;
    }

    @include below-mobile {
        &__categories {
            display: none;
        }

        &__categories-mobile {
            display: block;
            margin-bottom: 60px;
        }
    }
}

.contact {
    line-height: $line-height-lg;

    &__aside {
        margin-top: -160px !important;
    }

    &__employees {
        margin-top: 160px;
    }

    @include below-mobile {
        padding-bottom: 100px !important;

        &__employees {
            margin-top: 100px;
        }
    }
}

.employees {
    &__group {
        display: flex;
        margin: 0 -40px;

        &:not(:last-child) {
            margin-bottom: 40px;
        }
    }

    &__heading, &__people {
        padding: 0 40px;
    }

    &__heading {
        flex: 1;
    }

    &__people {
        flex: 3;
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -20px;
    }

    &__item {
        flex: 1;
        padding: 0 20px;
        margin-bottom: 40px;
    }

    &__name {
        margin: 0;
        line-height: $line-height-sm;
    }

    &__group-title {
        margin: 0;
    }

    &__role {
        display: block;
        font-size: $font-size-sm;
        color: $color-gray-light;
    }

    &__contacts {
        margin-top: 20px;
    }

    @include below-mobile {
        &__group {
            flex-direction: column;
            margin: 0;
        }

        &__heading, &__people {
            padding: 0;
        }

        &__heading {
            padding-bottom: 16px;
            border-bottom: 1px solid $color05;
            margin-bottom: 60px;
        }
    }
}

.sidebar-map {
    background: $color-white;
    border-radius: $border-radius;
    min-height: 360px;
    min-width: 600px;
    padding: 20px;

    @include below-desktop-lg {
        min-width: 480px;
    }
}

#map {
    height: 600px;
    width: 100%;
}