/******************************************************************
NAVIGATION STYLES
Styles for various navigation components used throughout the site
******************************************************************/

.sidebar-nav {
    padding: 60px 30px;
    min-height: 480px;
    min-width: 320px;
    background: $color03;
    color: $color-white;
    border-radius: $border-radius 0 $border-radius 0;
    
    &__title {
        padding: 0 20px 16px 20px;
        border-bottom: 1px solid rgba($color-white, $opacity-lg);
        margin-bottom: 20px;
        line-height: $line-height-sm;
    }

    &__items {
        padding: 0 20px;
    }

    &__item, .menu__item {
        position: relative;
        display: block;
        padding: 15px 0;

        &.is-active {
            padding-left: 35px;

            &:before {
                content: "";
                position: absolute;
                left: 0; top: 23px;
                height: 20px;
                width: 20px;
                background: url('../../assets/icons/icon-arrow-right-white.svg');
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
    }

    &__anchor, .menu__anchor {
        @extend .has-link-color-white;
    }
}