/******************************************************************
SEARCH STYLES
Styles related to search components, forms etc
******************************************************************/

.search-form {
    position: relative;

    .search-form__input {
        width: 100%;
        max-width: 100%;

        &::-webkit-search-decoration,
        &::-webkit-search-cancel-button,
        &::-webkit-search-results-button,
        &::-webkit-search-results-decoration { 
            display: none; 
        }
    }

    &__submit {
        position: absolute;
        top: calc(50% - 16px);
        right: 14px;
    }

    // MODS
    &--header {
        .search-form__input {
            border-radius: 0;
            margin: 0;
            height: 40px;
            font-size: $font-size-sm;
            min-width: 320px;
        }

        .search-form__submit {
            position: absolute;
            top: calc(50% - 10px);
            right: 20px;
            padding: 0;
            
            .icon {
                height: 20px;
                width: 20px;
            }
        }

        .search-form__label {
            display: none;
        }
    }

    &--lg {
        .search-form__input {
            height: 80px;
        }

        .search-form__label {
            top: 12px;
        }
    }
}

.search-results {
    &__form {
        padding: 100px 0 40px 0;
    }

    &__error {
        text-align: center;
    }

    &__title {
        display: none;
        text-align: center;
        font-style: italic;
        color: $color-gray;
    }

    @include below-mobile {
        &__form {
            padding: 60px 0 20px 0;
        }

        &__title {
            display: block;
        }
    }
}