/******************************************************************
ANIMATIONS
Various animations to be used throughout the project
******************************************************************/

@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
.anim-fadeout {
    animation: fadeOut $trans-time-lg $trans-type;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.anim-fadein {
    animation: fadeIn $trans-time-lg $trans-type;
}

@keyframes loader {
	0%,
	80%,
	100% {
        transform: scale(1);
	}
	40% {
        transform: scale(1.36);
	}
}
.anim-loader {
    animation: loader $trans-time-lg $trans-type infinite;
}

@keyframes buttonIconBounceRight {
    0% {
        opacity: 1;
        transform: translateX(0px);
    }
    25% {
        opacity: 0;
        transform:translateX(12px);
    }
    26% {
        opacity: 0;
        transform:translateX(-12px);
    }
    55% {
        opacity: 1;
        transform: translateX(0px);
    }
}
@keyframes buttonIconBounceLeft {
    0% {
        opacity: 1;
        transform: translateX(0px);
    }
    25% {
        opacity: 0;
        transform:translateX(-12px);
    }
    26% {
        opacity: 0;
        transform:translateX(12px);
    }
    55% {
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes slideRight {
    0% {
        opacity: 0;
        transform: scale(1.1) translateX(-1.6%);
    }
    1% {
        opacity: 0;
    }
    10% {
        opacity: 1;
    }
    90% {
        opacity: 1;
    }
    99% {
        opacity: 0;
    }
    100% {
        opacity: 0;
        transform: scale(1.1) translateX(1.6%);
    }
}