/******************************************************************
BREADCRUMB STYLES
Styles for breadcrumb components
******************************************************************/

.breadcrumbs {
    display: flex;
    align-items: center;
}

.breadcrumb-list {
    &__item {
        display: inline-block;
        font-size: $font-size-sm;

        &:after {
            content: "/";
            padding: 0 16px;
        }

        &:last-child {
            &:after {
                display: none;
            }
        }
    }

    &__anchor {
        @extend .has-link-color-black;
    }
}