/******************************************************************
LIST STYLES
Styles for various layout lists that contain granular items
******************************************************************/


.icon-list {
    &__item {
        position: relative;
        padding-left: 40px;
    }

    &__icon {
        position: absolute;
        left: 0;
        top: 7px;
    }
}

.blog-list {
    &__item {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
}

.months-list {
    display: flex;
}

.events-list {
    &__item {
        &:not(:last-child) {
            margin-bottom: 20px;
        }

        &:not(:first-child) {
            .events-list__label {
                margin-top: 40px;
            }
        }
    }

    &__label {
        color: $color02;
        text-transform: uppercase;
        margin: 0;
        font-size: $font-size-sm;
    }
}

.socials-list {
    margin: 0 -4px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    &__item {
        padding: 0 4px;
        margin-bottom: 4px;
    }

    &__anchor {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 28px;
        width: 28px;
        background: $color-white;
        color: $color03;
        border-radius: 40px;
        line-height: 1;
        transition: $transition-default;

        &:hover {
            color: $color-white;
            background: $color02;
            transition: $transition-default;
        }
    }

    .icon {
        height: 16px;
        width: 16px;
    }

    // MODS
    &--nowrap {
        flex-wrap: nowrap;
        
        .socials-list__item {
            margin-bottom: 0;
        }
    }
}

.category-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -8px;

    &__item {
        padding: 0 8px;
        margin-bottom: 16px;
    }

    &__anchor {
        display: block;
        padding: 8px 24px;
        border-radius: $border-radius 0 $border-radius 0;
        background: $color-white;
        color: $color02;
        transition: $transition-default;

        &:hover, &.is-active {
            background: $color02;
            color: $color-white;
            transition: $transition-default;
        }
    }
}