/******************************************************************
MODULE STYLES
Reusable/modular content blocks that make up the site layout
******************************************************************/

.page-heading {
    display: flex;
    align-items: flex-end;
    position: relative;
    height: 420px;
    background-color: $color05;
    background-size: cover;
    background-position: 50% 50%;

    &--has-sidebar {
        padding-right: 400px;
    }
}

.page-content {
    position: relative;
    padding: 0 0 200px 0;

    &:before {
        content: "";
        position: absolute;
        left: 0; top: -161px; right: 0; bottom: 0;
        width: 100%;
        height: 162px;
        background-image: url('../../assets/images/bg-wave-loop.svg');
        background-repeat: repeat-x;
        background-size: 2100px 162px;
        background-position: -160px top;
        z-index: 0;
    }

    &__inner {
        display: flex;
        margin: 0 -30px;
    }

    &__main {
        flex: 6;
        padding: 0 30px;
        min-height: 240px;
        z-index: 2;
    }

    &__aside {
        position: relative;
        flex: 2;
        padding: 0 30px;
        margin-top: -200px;
        z-index: 2;
    }

    &__heading {
        margin-top: -40px;
        margin-bottom: 40px;
    }

    &__title {
        color: $color03;
        margin: 0 0 10px 0;
    }

    &__breadcrumbs {
        padding-bottom: 20px;
        border-bottom: 1px solid $color05;
    }

    @include below-desktop {
        &__aside {
            display: none;
        }
    }

    @include above-desktop-xxl {
        &:before {
            background-position: 160px top;
        }
    }

    @include above-desktop-uw {
        &:before {
            background-position: 240px top;
        }
    }
}

.circular-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80px;
    width: 80px;
    border: 2px solid $color03;
    border-radius: 40px;
    padding: 10px;
    color: $color03;
    background: $color-white;

    .icon {
        height: 40px;
        width: 40px;
    }
}

.quicklink {
    display: block;
    background: $color-white;
    //border: 1px solid $color05;
    //border-radius: 18px;
    border-bottom-right-radius: 18px;
    border-top-left-radius: 18px;
    transition: $transition-default;

    &:hover {
        background: $color02;
        border-color: $color02;
        color: $color-white;
        transform: translateY(-8px);
        transition: $transition-default;
        
        .quicklink__icon {
            color: $color02;
            transition: $transition-default;
        }

        .circular-icon {
            border-color: $color-white;
            transition: $transition-default;
        }
    }

    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 40px 24px;
    }

    &__icon {
        transition: $transition-default;
    }

    &__title {
        margin: 10px 0 0 0;
    }
}

.section-heading {
    margin-bottom: 60px;

    &__title {
        margin: 0;
        color: $color03;
    }

    &__more {
        color: $color02;
        text-decoration: underline;

        &:hover {
            color: rgba($color02, $opacity-md);
        }
    }

    // MODIFIERS
    &--has-options {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .section-heading__options {
            margin-left: 60px;
        }
    }

    &--color-light {
        color: $color-white;

        .section-heading__more {
            color: $color-white;

            &:hover {
                color: rgba($color-white, $opacity-md);
            }
        }
    }

    &--centered {
        text-align: center;
    }

    @include below-desktop {
        text-align: center;
    }

    @include below-mobile {
        margin-bottom: 80px;

        &--has-options {
            display: block;

            .section-heading__title {
                margin-bottom: 6px;
            }

            .section-heading__options {
                margin-left: 0;
            }
        }
    }

    @include below-mobile-sm {
        margin-bottom: 60px;
    }
}

.section-options {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -20px;
    margin-right: -20px;
    
    &__item {
        padding: 0 20px;
        margin-bottom: 20px;
    }

    &__anchor {
        text-decoration: underline;
    }
}

.event-dates {
    margin-top: 16px;
    padding-bottom: 20px;
    border-bottom: 1px solid $color05;

    &__start, &__end {
        font-weight: $font-weight-bold;
    }
}