.slider {
    position: relative;

    &__track {
        overflow: hidden;
    }

    &__items {
        display: flex;
    }

    &__item {
        flex: 1;
    }

    &__dots {
        display: flex;
        justify-content: center;
        padding-bottom: 2px;
    }

    &__dot {
        display: inline-block;
        margin: 0 4px;
        padding: 0;
        height: 6px;
        width: 6px;
        background: rgba($color02, 0.32);
        border-radius: 50px;
        transition: $transition-default;

        &:hover {
            background: rgba($color02, 0.6);
            transition: $transition-default;
        }
    }
    &__dot--active {
        background: rgba($color02, 1);
        transform: scale(1.333334);
    }

    // MODIFIERS 
    &--with-peek {
        overflow: initial;
    }
}

.slider-controls {
    &__arrow {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $color-white;
        color: $color01;
        height: 50px;
        width: 50px;
        border-radius: 25px;
        transition: $transition-default;

        &:hover {
            background-color: $color02;
            color: $color-white;
            transition: $transition-default;
        }
    }

    &__arrow--prev {
        @extend .has-animated-icon-right;
        transform: scaleX(-1);

        position: absolute;
        left: -70px;
        top: calc(50% - 25px);
    }

    &__arrow--next {
        @extend .has-animated-icon-right;

        position: absolute;
        right: -70px;
        top: calc(50% - 25px);
    }

    &__icon {
        height: 22px;
        width: 22px;
    }

    @include below-desktop-xxl {
        &__arrow--prev {
            left: -25px;
        }

        &__arrow--next {
            right: -25px;
        }
    }

    @include below-mobile {
        &__arrow {
            height: 48px;
            width: 48px;
        }

        &__arrow--prev {
            left: -20px;
        }

        &__arrow--next {
            right: -20px;
        }
    }
}

.months-slider {
    .slider__track {
        padding-top: 8px;
    }
}