/******************************************************************
MISC SMALL COMPONENTS
Various tiny helper components that don't belong to any category
******************************************************************/


.separator {
    display: block;
    width: 100%;
    height: 1px;
    background: $color05;
    font-size: 0;
    margin: 60px 0;

    &--sm {
        display: inline-block;
        margin: 42px 0 40px 0;
    }

    &--wave {
        height: 40px;
        background: url('../../assets/images/wave-separator-loop.svg') repeat-x;
        background-size: 40px 40px;
    }
}

.scroll-anchor {
    position: absolute;
    top: -20px; // Set this to the desired height offset
    visibility: hidden;
    opacity: 0;

    &--center {
        top: 40vh;
    }

    @include below-desktop-xl {
        //top: -15px;
    }
    @include below-mobile {
        //top: -10px;
    }
}

.video-player {
    display: block;
    width: 100%;
    min-width: 100%;
    max-width: 100%;
}