/******************************************************************
GALLERY STYLES
Styling for gallery component
******************************************************************/


.gallery {
    $self: &;

    list-style-type: none;
    padding: 0;

    &__item {
        max-width: 25%;
        IMG {
            display: block;
            width: 100%;
            //background-color: $color-gray-lightest;
            //border: 2px solid $color-gray-lightest;
            box-shadow: inset 0 0 0 2px $color-gray-lightest;
        }
        A {
            display: block;
            opacity: 1;
            transition: $transition-default;
            &:hover {
                opacity: 0.75;
                transition: $transition-default;
            }
        }
    }

    &__caption {
        display: block;
        margin: 10px 0;
        color: $color-gray;
        font-size: 16px;
        font-weight: $font-weight-normal;
        text-align: center;
        word-wrap: break-word;
    }

    @include below-mobile {
        flex-wrap: wrap;
        &__item {
            max-width: 50%;
            flex: 1 0 50%;
            margin-bottom: 20px;
        }
    }
    @include below-mobile-sm {
        display: block;

        &__item {
            display: block;
            max-width: 100%;
            margin-bottom: 30px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.gallery-image {
    &__img {
        position: relative;
        width: 100%;
        padding-top: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
    }
}

.image-gallery {
    &__items {
        padding-top: 8px;
    }
    
    &__anchor {
        display: block;
        border-radius: $border-radius 0 $border-radius 0;
        overflow: hidden;
        transition: $transition-default;

        &:hover {
            transform: translateY(-8px);
            transition: $transition-default;
        }
    }
}