/******************************************************************
TABS STYLES
Styling for nav tabs
******************************************************************/


.nav-tabs {
    margin: 40px 0;
    border-bottom: 2px solid $color-gray;
    width: 100%;

    &__items {
        display: block;
        position: relative;
        padding: 0;
        margin: 0;
        max-width: 80%;
        margin: 0 auto;
    }

    &__item {
        display: inline;
        text-align: center;
        cursor: pointer;
    }

    &__anchor {
        display: inline-block;
        padding: 20px;
    }

    &__marker {
        position: absolute;
        bottom: -1px; left: 0; right: 0;
        height: 3px;
        width: 33%;
        margin: 0;
        background: $color01;
        border: 0;
        transition: 0.32s $trans-type;
    }

    &--three {
        .nav-tabs__anchor {
            width: 33%;
        }
        .nav-tabs__marker {
            width: 33%;
        }
        .nav-tabs__item:nth-child(1).is-active ~ .nav-tabs__marker, .nav-tabs__item:nth-child(1):hover ~ .nav-tabs__marker {
            margin-left: 0;
        }
        .nav-tabs__item:nth-child(2).is-active ~ .nav-tabs__marker, .nav-tabs__item:nth-child(2):hover ~ .nav-tabs__marker {
            margin-left: 33%;
        }
        .nav-tabs__item:nth-child(3).is-active ~ .nav-tabs__marker, .nav-tabs__item:nth-child(3):hover ~ .nav-tabs__marker {
            margin-left: 66%;
        }
    }

    @include below-mobile {
        &__items {
            max-width: 100%;
        }
    }
}

.tab-content {
    height: 0;
    opacity: 0;
    visibility: hidden;
    transition: visibility 0s linear, opacity 0s linear;

    &.is-active {
        height: auto;
        opacity: 1;
        visibility: visible;
        transition: visibility $trans-time linear, opacity $trans-time linear;
    }
}