/******************************************************************
PAGINATION STYLES
Styles for pagination components
******************************************************************/


.pagination {
    &__items {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -8px;
    }

    &__item {
        padding: 0 8px;
        margin-bottom: 16px;
    }

    &__anchor {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 50px;
        width: 50px;
        padding: 5px;
        border-radius: 25px;
        background: $color-white;
        color: $color02;
        transition: $transition-default;

        &:hover, &.is-active {
            background: $color02;
            color: $color-white;
            transition: $transition-default;
        }
    }

    &__icon {
        height: 20px;
        width: 20px;
    }
}