/******************************************************************
FOOTER STYLES
Styles specific to the site footer
******************************************************************/

.footer {
    padding: 30px 0 80px 0;
    background: $color-white;
    color: $color-gray-dark;

    &__anchor {
        color: $color-gray-dark;

        &:hover {
            color: rgba($color-gray-dark, $opacity-lg);
        }
    }
}

.footer-info {
    display: flex;
    justify-content: center;
    margin: 0 -30px;
    flex-wrap: wrap;

    &__item {
        padding: 4px 30px;

        &:not(:last-child) {
            border-right: 1px solid $color03;
        }
    }

    @include below-mobile {
        margin: 0 -20px;

        &__item {
            padding: 4px 20px;
            
            &:not(:last-child) {
                border-right: none;
                margin-bottom: 8px;
            }
        }
    }

    @include below-mobile-sm {
        flex-direction: column;
        align-items: center;
    }
}